import React from 'react';

function AboutAI() {
  return (
    <div>
      <h1>About AI</h1>
      <p>Artificial Intelligence (AI) is the simulation of human intelligence processes by machines, especially computer systems. These processes include learning, reasoning, and self-correction. AI applications include expert systems, speech recognition, and machine vision.</p>
    </div>
  );
}

export default AboutAI;
